import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { stipulateAmountOfScholarship } from 'dump/data';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    sys: '',
    grad: '',
    gradCode: '',
    fromScores10: '',
    fromScores4: '',
    fromTrainingScore: '',
    amountOfMoney: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formStipulate' })
class FormStipulateAmountOfScholarship extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = stipulateAmountOfScholarship.filter(teacher => teacher.code === code)[0];
    console.log(teacherMap);
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card
        title={teacherId ? 'Cập nhật quy định số tiền học bổng' : 'Thêm quy định số tiền học bổng'}
      >
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Hệ" hasFeedback>
              {getFieldDecorator('sys', {
                initialValue: initialValue.sys,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select disabled={teacherId} defaultValue="lucy">
                  <Option value="jack">Đại học tín chỉ</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Xếp loại" hasFeedback>
              {getFieldDecorator('grad', {
                initialValue: initialValue.grad,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={teacherId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mã xếp loại" hasFeedback>
              {getFieldDecorator('gradCode', {
                initialValue: initialValue.gradCode,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={teacherId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 10" hasFeedback>
              {getFieldDecorator('fromScores10', {
                initialValue: initialValue.fromScores10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 4" hasFeedback>
              {getFieldDecorator('fromScores4', {
                initialValue: initialValue.fromScores4,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={teacherId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm RL" hasFeedback>
              {getFieldDecorator('fromTrainingScore', {
                initialValue: initialValue.fromTrainingScore,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={teacherId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tiền" hasFeedback>
              {getFieldDecorator('amountOfMoney', {
                initialValue: initialValue.amountOfMoney,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#scholarship/stipulate" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStipulateAmountOfScholarship {...props} />
  </Page>
));



